@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Roboto', sans-serif;
}

.App {
  margin-top: 2em;
}

.creation-wrapper {
  display: flex;
  flex-direction: column;
  gap: 1em;
  padding: 2em;
  border-radius: 1em;
  background: rgba(255, 0, 0, 0.542);
}

.creation-title {
  width: max-content;
  margin: 0 auto;
  color: white;
}

.creation-input {
  width: 80%;
}

.creation-input,
.creation-btn {
  border: none;
  font-size: 1.5em;
  padding: 0.75rem 1.25rem;
}

.creation-input:focus,
.creation-btn:focus {
  outline: none;
}

.creation-btn {
  width: 20%;
  cursor: pointer;
}

.creation-btn:hover {
  background: rgb(228, 228, 228);
}

.all-todos-wrapper {
  display: flex;
  flex-direction: column;
  gap: 1em;
}

.todo-container {
  display: flex;
  font-size: 1.5em;
  background: rgba(0, 0, 0, 0.169);
  color: white;
}

.todo-container::selection {
  color: none;
  background: none;
}

.todo {
  z-index: 9999;
  padding: 1em;
  width: 100%;
  transition: background 0.25s;
}

.todo:hover {
  background: rgba(130, 58, 58, 0.455);
}

.x-container {
  padding: 1em;
  margin-left: auto;
  border-left: 1px solid rgba(255, 255, 255, 0.176);
  cursor: pointer;
  transition: background 0.25s;
}

.x-container:hover {
  background: rgba(130, 58, 58, 0.236);
}

.x-container:hover > .x {
  color: white;
  transform: scaleX(1.25) rotate(180deg);
}

.x {
  transform: scaleX(1.25);
  font-weight: 100;
  transition: transform 0.25s;
  color: rgba(255, 255, 255, 0.462);
}

.x::selection {
  color: none;
  background: none;
}
